import { processLayers } from './assets';
const processGroupLayerAssets = ({ page, layer, sketch }) => {
    return new Promise((resolve, reject) => {
        const layerImages = [];
        processLayers({
            page: page,
            layers: layer.layers,
            sketch: sketch
        })
            .then((groupLayerImages) => {
            const images = groupLayerImages.reduce((imageArray, layerImageArray) => {
                return [...imageArray, ...layerImageArray];
            }, []);
            layerImages.push(...images);
        })
            .finally(() => {
            resolve(layerImages);
        });
    });
};
export default processGroupLayerAssets;
