import { generateImageAsset, processLayerFills, processLayerBorders } from './assets';
const processLayerImage = ({ page, layer, sketch }) => {
    return new Promise((resolve, reject) => {
        // create image layer from image date
        const baseImage = new sketch.Image({
            image: layer.image,
            frame: layer.frame,
            parent: page
        });
        generateImageAsset({
            layer: baseImage,
            sketch: sketch,
            id: layer.id,
            prefix: '[image]'
        })
            .then((imageAsset) => {
            // remove asset artboard from page
            baseImage.remove();
            // return base64 image
            resolve(imageAsset);
        });
    });
};
const processImageLayerAssets = ({ page, layer, sketch }) => {
    return new Promise((resolve, reject) => {
        const { style } = layer;
        const { fills, borders, shadows, innerShadows } = style;
        const layerImages = [];
        processLayerImage({
            page: page,
            layer: layer,
            sketch: sketch
        })
            .then((layerImage) => {
            layerImages.push(layerImage);
            return processLayerFills({
                page: page,
                layer: layer,
                sketch: sketch,
                fills: fills
            });
        })
            .then((fillImages) => {
            const newImages = fillImages.filter(fillImage => fillImage);
            layerImages.push(...newImages);
            return processLayerBorders({
                page: page,
                layer: layer,
                sketch: sketch,
                borders: borders
            });
        })
            .then((borderImages) => {
            const newImages = borderImages.filter(borderImage => borderImage);
            layerImages.push(...newImages);
        })
            .finally(() => {
            resolve(layerImages);
        });
    });
};
export default processImageLayerAssets;
