import processImageLayerAssets from './imageAssets';
import processShapeLayerAssets from './shapeAssets';
import processGroupLayerAssets from './groupAssets';
export const clearLayerStyles = ({ layer, page }) => {
    return new Promise((resolve, reject) => {
        const { style, transform } = layer;
        if (layer.type === 'Text') {
            style.textColor = '#000000ff';
            layer.sketchObject.setTextBehaviour(0);
        }
        // @ts-ignore
        layer.parent = page;
        style.opacity = 1;
        style.borders = [];
        style.fills = [];
        style.shadows = [];
        style.innerShadows = [];
        style.blur = {
            blurType: 'Gaussian',
            radius: 0,
            motionAngle: 0,
            center: { x: 0, y: 0 },
            enabled: false
        };
        transform.flippedHorizontally = false;
        transform.flippedVertically = false;
        transform.rotation = 0;
        resolve(layer);
    });
};
export const generateImageAsset = ({ layer, sketch, id, prefix, scale }) => {
    return new Promise((resolve, reject) => {
        // create buffer from layer
        const buffer = sketch.export(layer, {
            formats: 'png',
            scales: scale ? scale : '1',
            output: false,
            ['save-for-web']: true
        });
        // create image layer from buffer
        const bufferImg = new sketch.Image({
            image: buffer
        });
        // get base64 from image layer nsdata
        const base64 = bufferImg.image.nsdata.base64EncodedStringWithOptions(0);
        // create base64 string
        const base64String = `data:image/png;base64,${base64}`;
        // return final image
        resolve({
            id: prefix ? `${prefix}${id}` : id,
            src: base64String,
        });
    });
};
const createGradientBorderImage = ({ page, layer, border, gradientOpacity, sketch, prefix }) => {
    return new Promise((resolve, reject) => {
        const padding = () => {
            switch (border.position) {
                case 'Outside':
                    return border.thickness;
                case 'Center':
                    return border.thickness / 2;
                case 'Inside':
                    return 0;
            }
        };
        // create new layer with gradient
        const gradientImage = new sketch.ShapePath({
            parent: page,
            frame: Object.assign(Object.assign({}, layer.frame), { width: layer.frame.width + padding(), height: layer.frame.height + padding() }),
            style: {
                fills: [{
                        fillType: 'Gradient',
                        gradient: border.gradient
                    }],
                borders: [],
                opacity: gradientOpacity
            }
        });
        // export image to temp dir
        generateImageAsset({
            layer: gradientImage,
            sketch: sketch,
            id: layer.id,
            prefix: prefix
        })
            .then((imageAsset) => {
            // remove image from page
            gradientImage.remove();
            // return final image
            resolve(imageAsset);
        });
    });
};
const createGradientFillImage = ({ page, layer, gradient, gradientOpacity, sketch, prefix }) => {
    return new Promise((resolve, reject) => {
        // create new layer with gradient
        const gradientImage = new sketch.ShapePath({
            parent: page,
            frame: layer.frame,
            style: {
                fills: [{
                        fillType: 'Gradient',
                        gradient: gradient
                    }],
                borders: [],
                opacity: gradientOpacity
            }
        });
        // export image to temp dir
        generateImageAsset({
            layer: gradientImage,
            sketch: sketch,
            id: layer.id,
            prefix: prefix
        })
            .then((imageAsset) => {
            // remove image from page
            gradientImage.remove();
            // return final image
            resolve(imageAsset);
        });
    });
};
const createPatternFillImage = ({ page, layer, pattern, fillOpacity, sketch, prefix }) => {
    return new Promise((resolve, reject) => {
        // create new layer with pattern
        // add transparent border to include any layer whitespace
        const patternImage = new sketch.ShapePath({
            parent: page,
            frame: layer.frame,
            style: {
                fills: [{
                        fillType: 'Pattern',
                        pattern: pattern
                    }],
                borders: [{
                        fillType: 'Color',
                        position: 'Inside',
                        color: '#00000001'
                    }],
                opacity: fillOpacity
            }
        });
        // generate base64 image from layer
        generateImageAsset({
            layer: patternImage,
            sketch: sketch,
            id: layer.id,
            prefix: prefix
        })
            .then((imageAsset) => {
            // remove image from page
            patternImage.remove();
            // return final base64 image
            resolve(imageAsset);
        });
    });
};
const processLayerBorderGradient = ({ page, layer, border, borderIndex, sketch }) => {
    return new Promise((resolve, reject) => {
        createGradientBorderImage({
            page: page,
            layer: layer,
            border: border,
            gradientOpacity: border.sketchObject.contextSettings().opacity(),
            sketch: sketch,
            prefix: `[border-${borderIndex}]`
        })
            .then((gradientBorderImage) => {
            resolve(gradientBorderImage);
        });
    });
};
const processLayerFillGradient = ({ page, layer, fill, fillIndex, sketch }) => {
    return new Promise((resolve, reject) => {
        createGradientFillImage({
            page: page,
            layer: layer,
            gradient: fill.gradient,
            gradientOpacity: fill.sketchObject.contextSettings().opacity(),
            sketch: sketch,
            prefix: `[fill-${fillIndex}]`
        })
            .then((gradientFillImage) => {
            resolve(gradientFillImage);
        });
    });
};
const processLayerFillPattern = ({ page, layer, fill, fillIndex, sketch }) => {
    return new Promise((resolve, reject) => {
        if (fill.pattern.image) {
            createPatternFillImage({
                page: page,
                layer: layer,
                pattern: fill.pattern,
                fillOpacity: fill.sketchObject.contextSettings().opacity(),
                sketch: sketch,
                prefix: `[fill-${fillIndex}]`
            })
                .then((patternFillImage) => {
                resolve(patternFillImage);
            });
        }
        else {
            resolve(null);
        }
    });
};
const processLayerFill = ({ page, layer, fill, fillIndex, sketch }) => {
    return new Promise((resolve, reject) => {
        switch (fill.fillType) {
            case 'Pattern':
                processLayerFillPattern({
                    page: page,
                    layer: layer,
                    fill: fill,
                    fillIndex: fillIndex,
                    sketch: sketch
                })
                    .then((layerImage) => {
                    resolve(layerImage);
                });
                break;
            case 'Gradient':
                processLayerFillGradient({
                    page: page,
                    layer: layer,
                    fill: fill,
                    fillIndex: fillIndex,
                    sketch: sketch
                })
                    .then((layerImage) => {
                    resolve(layerImage);
                });
                break;
            case 'Color':
                resolve(null);
                break;
        }
    });
};
export const processLayerFills = ({ page, layer, sketch, fills }) => {
    let promises = [];
    fills.forEach((fill, fillIndex) => {
        if (fill.enabled) {
            promises.push(processLayerFill({
                page: page,
                layer: layer,
                fill: fill,
                fillIndex: fillIndex,
                sketch: sketch
            }));
        }
    });
    return Promise.all(promises);
};
const processLayerBorder = ({ page, layer, border, borderIndex, sketch }) => {
    return new Promise((resolve, reject) => {
        switch (border.fillType) {
            case 'Gradient':
                processLayerBorderGradient({
                    page: page,
                    layer: layer,
                    border: border,
                    borderIndex: borderIndex,
                    sketch: sketch
                })
                    .then((layerImage) => {
                    resolve(layerImage);
                });
                break;
            case 'Pattern':
            case 'Color':
                resolve(null);
                break;
        }
    });
};
export const processLayerBorders = ({ page, layer, sketch, borders }) => {
    let promises = [];
    borders.forEach((border, borderIndex) => {
        if (border.enabled) {
            promises.push(processLayerBorder({
                page: page,
                layer: layer,
                border: border,
                borderIndex: borderIndex,
                sketch: sketch
            }));
        }
    });
    return Promise.all(promises);
};
const processLayer = ({ page, layer, sketch }) => {
    return new Promise((resolve, reject) => {
        const layerImages = [];
        switch (layer.type) {
            case 'Image':
                processImageLayerAssets({
                    page: page,
                    layer: layer,
                    sketch: sketch
                })
                    .then((imageLayerImages) => {
                    layerImages.push(...imageLayerImages);
                })
                    .finally(() => {
                    resolve(layerImages);
                });
                break;
            case 'Shape':
            case 'ShapePath':
                processShapeLayerAssets({
                    page: page,
                    layer: layer,
                    sketch: sketch
                })
                    .then((shapeLayerImages) => {
                    layerImages.push(...shapeLayerImages);
                })
                    .finally(() => {
                    resolve(layerImages);
                });
                break;
            case 'Group':
                processGroupLayerAssets({
                    page: page,
                    layer: layer,
                    sketch: sketch
                })
                    .then((groupLayerImages) => {
                    layerImages.push(...groupLayerImages);
                })
                    .finally(() => {
                    resolve(layerImages);
                });
                break;
            default:
                resolve(layerImages);
        }
    });
};
export const processLayers = ({ page, layers, sketch }) => {
    const promises = [];
    layers.forEach((layer) => {
        promises.push(processLayer({
            page: page,
            layer: layer,
            sketch: sketch
        }));
    });
    return Promise.all(promises);
};
export const getAssets = ({ page, artboard, sketch }) => {
    return new Promise((resolve, reject) => {
        processLayers({
            page: page,
            layers: artboard.layers,
            sketch: sketch
        })
            .then((layerImages) => {
            const images = layerImages.reduce((imageArray, layerImageArray) => {
                return [...imageArray, ...layerImageArray];
            }, []);
            resolve(images);
        });
    });
};
export default getAssets;
